import styles from "./gig_page.module.css"

import Header from "../components/header/header";
import Footer from "../components/footer/footer";

import Image from "next/image";;
import Link from "next/link"

import zapzap from "./../../images/icones/zapzap-branco.svg"
import casa from "./../../images/icones/casa.svg"

export const metadata = {
    title: 'Produto'
}

export default function Product() {
    return (
        <>
            <Header />
            <div className={styles.page_wrapper}>
                <div className={styles.page}>
                    <main className={styles.main}>
                        <div className={styles.main_history}>
                            <ul>
                                <li id="">
                                    <Link href="/">
                                        <Image height={20} width={20} src={casa} alt="house.icon"></Image>
                                    </Link>
                                </li>
                            </ul>
                        </div>
                    </main>
                    <div className={styles.sidebar_outer_wrapper}>
                        <div className={styles.sidebar_inner_wrapper}>
                            <div className={styles.sidebar_content}>
                                <div className={styles.sidebar_box}>
                                    <div className={styles.sidebar_description}></div>
                                    <div className={styles.sidebar_contact}>
                                        <Link href="" className={styles.sidebar_button}>
                                            <Image
                                                src={zapzap}
                                                width={30}
                                                height={30}
                                                alt="whatsapp.icon"
                                            />
                                            <p>Entre em contato comigo</p>
                                        </Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
};